html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Lato Light";
  src: url("../fonts/lato-lig-webfont.woff2") format("woff2"), url("../fonts/lato-lig-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Lato Bold";
  src: url("../fonts/lato-bol-webfont.woff2") format("woff2"), url("../fonts/lato-bol-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Lato Regular";
  src: url("../fonts/lato-reg-webfont.woff2") format("woff2"), url("../fonts/lato-reg-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Lato Hairlane";
  src: url("../fonts/lato-hai-webfont.woff2") format("woff2"), url("../fonts/lato-hai-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.icon-burger {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-chef {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-money {
  width: 1.88em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  font-family: 'Lato Light', sans-serif;
}

.layout {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.header .layout {
  font-size: 0;
}

.header__logo {
  display: inline-block;
  vertical-align: middle;
  padding: 30px 0;
  width: 76px;
  text-decoration: none;
}

.navigation {
  display: inline-block;
  vertical-align: middle;
  width: calc(100% - 76px);
  text-align: right;
}

.navigation__item {
  display: inline-block;
  vertical-align: middle;
  padding-left: 29px;
}

.navigation__item span {
  background-color: #FFF;
  height: 2px;
  width: 14px;
  display: block;
  margin-bottom: 2px;
}

.navigation__item span:last-child {
  margin-bottom: 0;
}

.navigation__link {
  font-size: 14px;
  display: block;
  text-decoration: none;
  color: #FFF;
}

.navigation__list {
  display: inline-block;
}

.hero {
  height: 100vh;
  text-align: center;
}

.hero:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.hero__in {
  display: inline-block;
  vertical-align: middle;
}

.hero__h2 {
  font-family: 'Times New Roman', serif;
  font-size: 36px;
  font-style: italic;
  color: #FFF;
  font-weight: 500;
}

.hero__h1 {
  font-family: 'Lato Bold', sans-serif;
  font-size: 100px;
  color: #FFF;
}

.hero__btn {
  font-family: 'Lato Bold', sans-serif;
  display: block;
  height: 62px;
  width: 275px;
  border: 3px solid #FFF;
  border-radius: 3px;
  color: #FFF;
  text-decoration: none;
  line-height: 62px;
  font-size: 18px;
  margin: 0 auto;
  margin-top: 30px;
  text-transform: uppercase;
}

.hero__h3 {
  font-family: 'Lato Regular', sans-serif;
  margin-top: 30px;
  font-size: 14px;
  color: #FFF;
  font-weight: 500;
}

.hero__link {
  font-family: 'Lato Bold', sans-serif;
  text-decoration: underline;
  color: #FFF;
}

.get-blocks {
  padding-top: 100px;
  padding-bottom: 100px;
  font-size: 0;
}

.get-block {
  display: inline-block;
  width: 33.33333%;
  font-size: 16px;
  text-align: center;
}

.get-block__icon {
  display: inline-block;
  width: 105px;
  height: 105px;
  border: 2px solid #000;
  border-radius: 50%;
  position: relative;
}

.get-block__icon .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 60px;
}

.get-block__icon .icon-money {
  font-size: 43px;
}

.get-block__title {
  font-family: 'Lato Bold', sans-serif;
  font-size: 18px;
  margin-top: 20px;
}

.get-block__title:after {
  display: block;
  content: '';
  border-bottom: 1px solid #f57365;
  width: 88px;
  margin: 10px auto 0;
}

.get-block__text {
  font-size: 16px;
  padding: 0 25px;
  margin-top: 20px;
}

.app {
  background-color: #f7f7f7;
  padding-top: 120px;
  width: 100%;
}

.app:after {
  content: " ";
  display: table;
  clear: both;
}

.app .layout {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
}

.app__iphone {
  width: 40%;
  max-width: 425px;
  height: auto;
}

.app__iphone img {
  width: 100%;
}

.app__content {
  width: 60%;
  padding-left: 100px;
  padding-top: 50px;
}

.app__title {
  font-family: 'Lato Bold', sans-serif;
  font-size: 18px;
  margin-top: 20px;
}

.app__title:after {
  display: block;
  content: '';
  border-bottom: 1px solid #f57365;
  width: 88px;
  margin: 10px 0;
}

.app__text {
  margin: 0 auto;
}

.app__text p {
  padding: 5px 0;
}

.app__stores {
  margin-top: 10px;
}

.app__stores img {
  padding-right: 15px;
}

.review {
  font-family: 'Lato Hairlane', sans-serif;
  padding-top: 100px;
  padding-bottom: 100px;
}

.review__content {
  text-align: center;
  margin-bottom: 20px;
}

.review__comment {
  font-size: 30px;
  font-weight: normal;
  color: #FFF;
  margin: 20px auto;
}

.review__name {
  font-size: 16px;
  font-weight: normal;
  color: #FFF;
}

.cuisines__title {
  font-family: 'Lato Bold', sans-serif;
  text-align: center;
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.cuisines__title:after {
  display: block;
  content: '';
  border-bottom: 1px solid #f57365;
  width: 88px;
  margin: 10px auto 0;
}

.cuisines__griditem {
  width: 20%;
  float: left;
  height: 225px;
  text-align: center;
}

.cuisines__recipes {
  font-family: 'Times New Roman', serif;
  font-size: 16px;
  font-style: italic;
  color: #FFF;
}

.cuisines__country {
  font-family: 'Lato Bold', sans-serif;
  font-size: 36px;
  color: #FFF;
}
