@include font('Lato Light', 'lato-lig-webfont');
@include font('Lato Bold', 'lato-bol-webfont');
@include font('Lato Regular', 'lato-reg-webfont');
@include font('Lato Hairlane', 'lato-hai-webfont');

@mixin lato-light {
	font-family: 'Lato Light', sans-serif; }
@mixin lato-bold {
	font-family: 'Lato Bold', sans-serif; }
@mixin lato-regular {
	font-family: 'Lato Regular', sans-serif; }
@mixin lato-hairlane {
	font-family: 'Lato Hairlane', sans-serif; }
